.wrapper {
	position: relative;
	display: flex;
	align-items: center;
	/* justify-content: space-between; */
	gap: 12px;
	padding: 8px 0;
}

.text {
	display: flex;
	align-items: center;
	justify-content: space-between;
	gap: 12px;
	width: 100%;
	position: relative;
	overflow: hidden;
}

.text:hover .description,
.text:focus .description {
	transform: translateY(0);
}

.title {
	flex-grow: 1;
}

.description {
	font-size: 10px;
	position: absolute;
	right: 0px;
	transform: translateX(100%);
	transition-property: transform;
	transition-duration: 350ms;
	transition-timing-function: ease-out;
}

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 32px;
	height: 32px;
	padding: 0;
	margin: 0;
	border: none;
	border-radius: 4px;
	background-color: transparent;
	cursor: pointer;
	color: #f44336;
	transition: background-color 200ms linear;
}

.edit {
	color: #000000;
}

.btn:hover {
	background-color: #e2e5e8;
}

.btn:active {
	background-color: #bdbdbd;
}

.checkbox {
	width: 24px;
	height: 24px;
	cursor: pointer;
}
